    height: 40px;
    width: 40px;
}

input::-webkit-input-placeholder {
    color:grey;
    font-weight: 1;
    font-size: 15px;
}

input:-ms-input-placeholder {
    color:grey;
    font-weight: 1;
    font-size: 15px;
}

input::-ms-input-placeholder {
    color:grey;
    font-weight: 1;
    font-size: 15px;
}

input::placeholder {
    color:grey;
    font-weight: 1;
    font-size: 15px;
}

.btn {
     height:50px;
     width:150px;
    background-color: cornflowerblue;
    border:none;
    outline-width: 0;
    cursor: pointer;
    margin-top: 20px;
    color:white;
    font-size: 14px;
}

.btn:hover {
    background-color:rgb(59, 93, 155);
}

@media screen and (max-width :500px) {
    .registration-info-1-1 {
        flex-direction: column;
    }
}

@media screen and (max-width: 1000px) {
