
.fixed-back > span {
    position: absolute;
    color:white;
    font-size:30px;
}

.fixed-back {
    background: url("./images/back-07.jpg") no-repeat center;
    background-size:cover;
    background-attachment: fixed;
    height:250px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.contact-image-2 {
    position: absolute;
    color:white;
    display: flex;
    align-items: center;
    justify-content: center;
    height:20px;
    width:100%;
    background-color:transparent;
    padding:20px 0;
    margin-top:-60px;
}

.contact-image-2 > span {
    font-size:12px;
    letter-spacing: 3px;
}

.contact-image-2 > span > span {
    color:burlywood ;
}

.contact-container {
    background-color: rgb(22, 22, 22);
}
.form-container {
    display: flex;
    flex-direction: row;
    color:white;
    justify-content: center;
    height:auto;
    padding:80px 120px;
    margin-right:80px;
    
}

.form-1 > input{
    height:25px;
    background-color:black;
    border:none;
    outline-width: 0;
    color:white;
    padding:10px;
    width:350px;
    margin-bottom: 15px;
}

.contactus-btn {
    height:45px;
    width: 180px;
    color:white;
    border:2px solid white;
    background-color: transparent;
    font-weight:600;
    font-size:14px;
    outline-width:0;
}

.contactus-btn:hover {
    color:rgb(22, 22, 22);
    background-color:burlywood ;
    border:none;
    cursor: pointer;
}

.contact-info {
    margin-left:40px;
    font-size:13px;
}

.message {
    color:white;
    background-color: black;
    opacity:0.8;
    border:none;
    outline-width: 0;
    margin-left:40px;
    width:400px;
    height:145px;
    padding:10px;
}

.message-container {
    margin-top:60px;
}

.message-hide {
    background-color: black;
    opacity:0.8;
    border:none;
    outline-width:0;
    padding:10px;
    color:white;
    display: none;
    width:auto;
}


.contact-info-2  >span >span {
    color: burlywood;
    margin-bottom: 20px;
    font-weight:500;
}

.contact-info-3 {
    display: none;
}

.contact-info-3 >span >span {
    color: burlywood;
    margin-bottom: 20px;
    font-weight:500;
}

.map {
    width:100%;
    height:400px;
}

@media only screen and (max-width: 1200px) {
    .message {
        display: none;
    }
    .message-hide {
        display: block;
        width:350px;
    }
    .btn {
        margin-top: 25px;;
    }
}

@media only screen and (max-width:1010px ) {
    
    .btn {
        margin-top: 25px;;
    }
    .form-container {
        padding-right:40px;
    }
    .form-1 > input {
        width:80vw;
    }
    .contact-info {
        display: none;
        margin-left: 10px;
        margin-right:10px;
    }
    .contact-info-3 {
        display: block;
        padding-top:2px;
        color:white;
        padding-left:7vw;
        padding-bottom: 50px;
    }
    .message-hide {
        width:80vw;
    }
}